.presentIn{
    margin: 4rem 4rem;
}
.presentIn h1{
    font-size: 3rem;
}
.row1{
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;
}
.row2{
    margin-top: 3rem;
    display: flex;
    justify-content: space-between;

}
.t-card{
    width:32%;
 
    box-shadow: 0 5px 25px 2px rgba(0,0,0,0.11);
    text-align: start;
    border-top-right-radius:7rem ;
    border-top-left-radius:7rem ;
    cursor: pointer;
    padding: 1rem .8rem;
}
.t-image{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 200px;
    overflow: hidden;
    border-radius: 7px;

}
.t-image img{
    width: 100%;
    height: 100%;
    transition: 0.3s ease-in-out;
    border-radius: 7px;
}
.t-image:hover img{
    transform: scale(1.3);
}
.t-card h4{
    font-size: 1.3rem;
    padding: .9rem 0.2rem 0;
}
@media screen and (max-width:850px){
    .presentIn{
        margin: 3rem;

    }
    .row1,.row2 {
        margin-top: 3rem;
        display: flex;
        justify-content: space-between;
        flex-direction: column;
    }
    .t-card{
        width: 100%;
        margin-bottom: 1.5rem;
    }

   
}